.footerInf{
    padding-top: 40px;
    display: flex;
    justify-content: space-between;
    .footerInfItem {
        width: 100%;
    
        .link {
          cursor: pointer;
          line-height: 34px;
          color: #414141;
          display: flex;
    
          .footerInfImg {
            padding-right: 8px;
          }
    
          .mailText {
            text-decoration: underline;
          }
        }
    
        .link:first-child {
          font-weight: 600;
          line-height: 13px;
          letter-spacing: 0.06em;
          margin-bottom: 8px;
        }
      }
}


@media all and (max-width: 814px) {
  .footerInf {  
      align-items: center; 
      flex-wrap: wrap;
      justify-content: space-between;
      row-gap: 30px;
    .footerInfItem{
      width: 35%;
    }
  }
}

@media all and (max-width: 520px) {
  .footerInf { 
    flex-direction: column;
    .footerInfItem{
    
      width: 100%;
      a.link {
        justify-content: center;
        height: 100%;
      }

      div.link{
        justify-content: center;
        height: 100%;
      }
    }
  }
}